	module.exports = {"data":{"post":{"html":"<p>Mama went to the doctor today. The ligament in her ankle is not completely torn, so surgery will not be necessary, but the ankle was still too swollen to apply the gel cast today. She'll get that on Wednesday. Meanwhile she is taking Arthotec for the pain and swelling as well as a set of injections against thrombosis. She can slide up and down the stairs, so she's sleeping upstairs again. She go back to work next week, but probably won't be able to drive for another week after that.</p>","fields":{"title":"Diagnosis T14.3","date":"20 Jan 2003","url":"https://papascott.de/2003-01-20-diagnosis-t143/"}}},"pathContext":{"slug":"/2003-01-20-diagnosis-t143/","prev":{"fields":{"title":"Injured Reserve","date":"18 Jan 2003","slug":"/2003-01-18-injured-reserve/"}},"next":{"fields":{"title":"Hobble Hobble","date":"22 Jan 2003","slug":"/2003-01-22-hobble-hobble/"}}}}

